@import "main-bx";

.map {
    padding: 110px 0 70px;

    @include below(1200px) {
        padding: 40px 0;
    }

    &__wrap {
        position: relative;
    }
    .map__tooltip {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        margin: 30px 30px 0;
        padding: 36px 20px 36px 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: rgba(255, 255, 255, 0.1);
        backdrop-filter: blur(50px);
        border-radius: 30px;

        span {
            font-weight: 500;
            font-size: 40px;
            line-height: 90%;
            color: #FFF;

            @include below(1024px) {
                font-size: 16px;
                line-height: 90%;
            }
        }

        svg {
            width: 72px;
            height: 72px;
            border-radius: 20px;
            box-shadow: 0px 4px 30px rgba(106, 168, 251, 0.5);

            @include below(1024px) {
                width: 36px;
                height: 36px;
                border-radius: 15px;
            }
        }

        @include below(1024px) {
            padding: 10px 10px 10px 24px;
            border-radius: 20px;
            margin: 10px 10px 0;
        }
    }

    .map__over {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0 , .7);
        border-radius: 40px;

        display: flex;
        align-items: center;
        justify-content: center;

        font-weight: 500;
        font-size: 25px;
        transition: $trns;
        color: transparent;
        cursor: pointer;

        &:hover {

            color: #fff;
        }

        @include below(1024px) {
            border-radius: 20px;
            font-size: 16px;
        }
    }

    &__container {
        background: #1A1A1A;
        border-radius: 40px;
        overflow: hidden;
        height: 670px;

        @include below(1024px) {
            height: 300px;
            border-radius: 20px;
        }
    }

    .ymaps-2-1-79-map {
        border-radius: 40px;
    }

    .ymaps-2-1-79-ground-pane {
        filter: invert(75%) sepia(75%) saturate(200%) hue-rotate(190deg) brightness(102%) contrast(97%);
    }

    .ymaps-2-1-79-copyrights-pane {
        display: none;
    }
}
