@import "main-bx";

.model-card {
    padding: 120px 0 80px;

    @include below(1240px) {
        padding: 40px 0;
    }

    &__wrap {
        &:not(:last-child) {
            margin-bottom: 175px;

            @include below(1240px) {
                margin-bottom: 100px;
            }

            @include below(768px) {
                margin-bottom: 60px;
            }
        }
    }

    &__item {
        display: flex;

        &_second {
            flex-direction: row-reverse;

            .model-card__title {
                text-align: right;
                margin-left: 0;
                
                @include below(768px) {
                    margin-right: 20px;
                }
            }

            @include above(769px) {
                
                .model-card__icon {
                    left: auto;
                    right: -65px;
                }

                .model-card__title {
                    margin-right: -10px;
                }

                .model-card__img {
                    transform: translate(65px, -40px);
                }
            }
        }

        @include below(768px) {
            display: block;
        }
    }

    &__title {
        letter-spacing: 0.2em;
        text-transform: uppercase;
        

        @include above(769px) {
            margin-left: -5px;
        }

        @include below(768px) {
            margin-left: 30px;
            margin-bottom: -30px;
        }
    }

    &__content {
        position: relative;
    }

    &__icon {
        width: 100%;
        max-width: 745px;
        position: absolute;
        z-index: 10;

        @include above(769px) {
            top: -10px;
            left: -50px;

            &.model-card__icon_md {
                left: -75px;
            }
        }

        img {
            width: 100%;
        }
    }

    &__img {
        width: 100%;
        max-width: 1000px;
        position: relative;
        z-index: 20;
        margin-bottom: 20px;

        @include above(769px) {
            transform: translate(-65px, -40px);
            margin-bottom: -40px;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__list {
        display: inline-flex;
        flex-direction: column;

        &_second {
            .model-card__list-item {
                align-self: flex-start;
                flex-direction: row-reverse;

                &::after {
                    margin-left: 0;
                    margin-right: 16px;
                }
            }

        }
    }

    &__list-item {
        padding: 20px;
        border: 1px solid #6AA8FB;
        border-radius: 20px;
        display: inline-flex;
        align-items: center;
        align-self: flex-end;
        white-space: nowrap;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        @include below(768px) {
            align-self: flex-start;
            flex-direction: row-reverse;
        }

        &::after {
            content: "";
            width: 12px;
            height: 12px;
            margin-left: 16px;
            border: 3px solid #6AA8FB;
            border-radius: 50%;

            @include below(768px) {
                width: 10px;
                height: 10px;
                margin-left: 0;
                margin-right: 16px;
            }
        }

        span {
            font-size: 16px;
            line-height: 90%;
            color: rgba(2, 2, 2, 0.8);

            @include below(768px) {
                font-size: 14px;
            }

            @include below(370px) {
                font-size: 12px;
            }
        }

        @include below(768px) {
            white-space: normal;
            border-radius: 15px;
            padding: 12px 20px;
        }
    }

    &__bottom {
        border: 1px solid #020202;
        padding: 40px;
        border-radius: 40px;
        display: flex;
        justify-content: space-between;

        @include below(1240px) {
            padding: 20px;
            margin-top: 20px;
            flex-direction: column;
            border-radius: 20px;
        }
    }

    &__price {
        display: flex;
        flex-direction: column;

        @include below(1240px) {
            margin-bottom: 40px;
        }
    }

    &__price-title {
        color: rgba(2, 2, 2, 0.5);
        margin-bottom: 21px;

        @include below(768px) {
            font-size: 16px;
            margin-bottom: 10px;
        }
    }

    &__numb {
        font-weight: 700;
        font-size: 54px;
        line-height: 90%;

        @include below(1240px) {
            font-size: 40px;
        }
    }

    &__text {
        margin-top: 5px;
        font-size: 12px;
        line-height: 90%;
        letter-spacing: 0.06em;
        color: rgba(2, 2, 2, 0.5);
        text-align: right;

        @include below(1240px) {
            text-align: left;
        }
    }

    &__right {
        display: flex;
    }

    &__btn {
        background-color: $blue_color;
        box-shadow: 0px 4px 30px rgba(106, 168, 251, 0.5);
        border-radius: 20px;
        padding: 20px;
        cursor: pointer;
        transition: all .3s linear;

        @include above(769px) {
           display: flex;
           align-items: center;
        }

        @include below(768px) {
            width: 50%;
        }

        &:hover {
            background-color: #4494FF;
        }

        &.is-active {
            background-color: #4494FF;

            svg[data-type="plus"] {
                display: none;
            }
        }

        &:last-child {
            margin-left: 20px;

            @include below(768px) {
                margin-left: 16px;
            }
        }

        &:not(.is-active) {
            svg[data-type="minus"] {
                display: none;
            }
        }
    }

    &__btn-icon {
        width: 40px;
        height: 40px;
        margin-right: 20px;

        @include below(768px) {
            margin-right: 0;
            margin-bottom: 10px;
            width: 30px;
            height: 30px;
        }
    }

    &__btn-text {
        font-weight: 500;
        font-size: 20px;
        line-height: 110%;
        color: #FFFFFF;
        margin-right: 40px;

        @include below(768px) {
            margin-right: 0;
            margin-bottom: 16px;
            font-size: 14px;
        }

        
        @include below(365px) {
            font-size: 12px;
        }
    }

    &__btn-switch {
        width: 44px;
        height: 44px;
        background-color: $white_color;
        border-radius: 15px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            width: 24px;
            height: 24px;

            @include below(768px) {
                width: 20px;
                height: 20px;
            }
        }

        @include below(768px) {
            width: 40px;
            height: 40px;
        }
    }

    &__tables {

    }

    &__table {
        display: none;

        margin-top: 10px;
        padding: 40px;

        border: 1px solid #6AA8FB;
        border-radius: 40px;

        @include below(1024px) {
            border-radius: 20px;
            padding: 20px;
        }
    }

    &__table-title {
        font-weight: 500;
        font-size: 32px;
        line-height: 90%;
        color: #6AA8FB;
        margin-bottom: 40px;

        @include below(1024px) {
            font-size: 16px;
            margin-bottom: 20px;
        }
    }

    &__table-flex {
        display: flex;
        justify-content: space-between;

        @include below(768px) {
            display: block;
        }
    
        ul {
            &:not(:last-child) {
               @include above(768px) {
                    margin-right: 32px;
               }
            }
        }

        ul li {
            font-size: 18px;
            color: #020202;

            @include below(1240px) {
                font-size: 14px;
            }
        }
    }

    &__tech {
        width: 100%;
        
        @include above(769px) {
            max-width: 556px;
        }
    }

    &__tech-item {
        line-height: 90%;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgba(2, 2, 2, 0.1);

        padding: 12px 0 8px;

        &:not(:last-child) {
            margin-bottom: 10px;
 
            @include below(1240px) {
                margin-bottom: 6px;
            }
        }

        b {
            text-align: center;
            min-width: 100px;

            @include below(1240px) {
                max-width: 60px;
            }
        }
    }

    &__equipment {
        list-style-type: disc;
        padding-left: 20px;

        width: 100%;
       
        @include above(769px) {
            max-width: 588px;
        }
    }

    &__equipment-item {
        line-height: 120%;

        &:not(:last-child) {
            margin-bottom: 12px;
        }

        &::marker {
            font-size: 14px;
            color: #6AA8FB;
        }
    }
}