@import "main-bx";

.test-drive {
    padding-bottom: 70px;

    @include below(1024px) {
        padding-bottom: 40px;
    }
    
    &__wrap {
        display: flex;
        justify-content: space-between;
        background-color: #6AA8FB;
        border-radius: 40px;
        padding: 60px;

        @include below(1024px) {
            border-radius: 20px;
            padding: 30px;
        }
    }

    &__content {
        width: 100%;
        max-width: 423px;
        margin-right: 32px;

        @include below(768px) {
            margin-right: 0;
            max-width: 100%;
        }
    }

    &__title {
        color: #fff;
        margin-bottom: 40px;

        @include below(1024px) {
            margin-bottom: 24px;
        }
    }

    &__form {
        .form__label {
            color: #fff;

            &:hover {
                .form__input {
                    border-color: #fff;
                }
            }
            
            &:not(:last-of-type) {
                margin-bottom: 20px;
            }
        }

        .form__input {
            color: #fff;
            background-color: transparent;
            border-color: rgba(255, 255, 255, 0.5);

            &::placeholder {
                color: #fff;
                opacity: 0.5;
            }
        
            &:focus {
                border-color: #fff;

                &::placeholder {
                    opacity: 1;
                }
            }
        }

        .btn {
            margin-top: 40px;

            @include below(600px) {
                width: 100%;
            }
        }
    }

    &__img {
        width: 50%;

        &:not(.test-drive__img_mobile) {
            @include below(1024px) {
                display: none;
            }
        }

        &.test-drive__img_mobile {
            width: 100%;
            margin-bottom: 30px;
            @include above(1025px) {
                display: none;
            }
        }


        img {
            border-radius: 30px;
            height: auto;

            @include below (1025px) {
                border-radius: 15px;
            }
        }
    }
}
