@import "main-bx";

.models {
    padding: 113px 0 90px;

    @include below(1024px) {
        padding: 0 0;

        position: fixed;
        top: 150px;
        left: 0;
        background-color: #fff;
        min-height: 100vh;
        overflow-x: hidden;
        overflow-y: auto;
        width: 100%;
        box-shadow: 11px 14px 18px 0px rgba(0,0,0,0.2);
        padding: 80px 40px 100px;
        z-index: 1000;
        transform: translateX(-200%);

        &.is-show {
            box-shadow: none;
            transform: translateX(0);
        }

        &.is-transition {
            transition: all .3s linear;
        }
    }

    @include below(620px) {
        top: 120px;
        padding: 80px 0 100px;
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(1240px) {
            flex-direction: column;
            align-items: stretch;
        }
    }

    &__left {
        @include below(1240px) {
           margin-bottom: 35px;
        }

        @include below(768px) {
            margin-bottom: 28px;
        }
    }

    &__right {
        display: flex;
        align-items: stretch;
        margin: 0 -6px;

        @include below(1024px) {
            flex-direction: column;
            margin: -12px 0 0;
        }
    }

    &__item {
        width: calc(33.33333% - 12px);
        margin: 0 6px;

        @include below(1024px) {
            width: 100%;
            margin: 12px 0 0;
            display: flex;
            align-items: center;
        }
    }

    &__img {

        @include below(1024px) {
            width: 150px;
        }

        img {
            border-radius: 40px;
            width: 100%;

            @include below(1024px) {
                border-radius: 15px;
            }
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        margin-top: 23px;
        font-weight: 500;

        @include below(1024px) {
            margin: 0 0 0 20px;
        }
    }

    &__name {

        letter-spacing: 0.06em;
        text-transform: uppercase;
        color: rgba(2, 2, 2, 0.5);

       
    }

    &__model {
        letter-spacing: 0.06em;
        text-transform: uppercase;
        color: $black_color;
    }
}