@import "main-bx";

.concept {
    padding: 90px 0 110px;

    @include below(1200px) {
        padding: 40px 0;
    }

    &__wrap {
        overflow: hidden;
        padding: 0 3.62%; 

        @include below(1024px) {
            padding: 0 14px;
        }
    }

    &__title {
        text-align: center;
        margin-bottom: 50px;

        @include below(1024px) {
            margin-bottom: 24px;
        }

        @include below(600px) {
            text-align: left;
        }
    }

    &__text {
        text-align: center;

        @include below(600px) {
            text-align: left;
        }
    }


    &__list {
        padding-top: 100px;
        display: flex;
        margin: -20px -22.5px 0;

        &.concept__list_mob {

            @include above(601px) {
                display: none;
            }
        }

        @include below(1024px) {
            margin: -20px -5px 0;
            padding-top: 32px;
        }

        @include below(600px) {
            &:not(.concept__list_mob) {
                display: none;
            }
            margin: -16px -8px 0;
        }

    }

    &__list-col {
        &:first-child {
            align-self: center;
        }

        @include below(600px) {
            margin: 0 8px;
        }
    }

    &__list-item {
        width: calc(20% - 45px);
        margin: 20px 22.5px 0;

        @include below(1024px) {
            width: calc(20% -10px);
            margin: 20px 5px 0;
        }

        @include below(1024px) {
            width: 100%;
            margin: 16px 0 0;
        }
        
        &:nth-child(1),
        &:nth-last-child(1) {
            align-self: center;

            @include below(600px) {
                align-self: initial;
            }
        }

        &:nth-child(2),
        &:nth-last-child(2) {
            align-self: flex-end;

            @include below(600px) {
                align-self: initial;
            }
        }

        img {
            border-radius: 40px;
            height: auto;

            @include below(1024px) {
                border-radius: 20px;
            }
        }
    }
}
