@import "main-bx";

.guarantees {
    padding: 80px 0 90px;

    @include below(1024px) {
        padding: 40px 0;
    }

    &__wrap {
        padding: 80px 124px 130px 80px;
        border-radius: 40px;
        background-color: #1A1A1A;
        color: $white_color;
        display: flex;
        justify-content: space-between;

        @include below(1240px) {
            padding: 80px 30px 80px;
        }

        @include below(1024px) {
            flex-direction: column;
            padding: 30px 30px 40px;
            border-radius: 20px;
        }
    }

    &__left {
        width: 100%;
        max-width: 446px;
    }

    &__title {
        margin-bottom: 40px;

        @include below(1024px) {
            margin-bottom: 24px;
        }
    }

    &__right {
        padding-top: 50px;

        @include below(1024px) {
            padding-top: 40px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;

        &:last-child {
            margin-top: 32px;
        }
    }

    &__item-text {
        font-size: 16px;
        line-height: 90%;
        color: rgba(255, 255, 255, 0.4);
        margin-bottom: 12px;

        @include below(479px) {
            margin-bottom: 8px;
            font-size: 14px;
        }
    }

    &__item-numb {
        font-size: 100px;
        line-height: 90%;

        @include below(1240px) {
            font-size: 85px;
        }

        @include below(1024px) {
            font-size: 70px;
        }

        @include below(479px) {
            font-size: 50px;
        }

        @include below(365px) {
            font-size: 40px;
        }
    }
}