@import "main-bx";

.header {
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 40px;

        @include below(1024px) {
            flex-wrap: wrap;
        }

        @include below(620px) {
            padding-top: 22px;
        }
    }

    &__logo {
        width: 100%;
        max-width: 240px;
        display: block;

        @include below(1024px) {
            max-width: 200px;
        }

        @include below(768px) {
            max-width: 180px;
        }

        @include below(500px) {
            max-width: 150px;
        }
    }

    &__contacts {
        display: flex;
        align-items: center;

        @include below(1240px) {
            flex-direction: column;
            align-items: flex-start;
            margin-left: 30px;
        }

        @include below(1024px) {
            margin-left: 0;
            margin-top: 26px;
            flex: 0 0 60%;
        }

        @include below(620px) {
            flex: 0 0 100%;
        }
    }

    &__contacts-item {
        display: flex;
        align-items: center;

        &:last-child {
            margin-left: 41px;

            @include below(1240px) {
                margin: 10px 0 0;
            }

            @include below(620px) {
                margin: 6px 0 0;
            }
        }
  
        svg {
            width: 14px;
            height: 14px;
            margin-right: 8px;

            @include below(620px) {
                display: none;
            }
        }

        a {
            transition: $trns;
            &:hover {
                color: $blue_color;
            }

            @include below(1024px) {
                font-weight: 600;
            }
        }
    }

    &__contacts-text {
        font-size: 15px;
        line-height: 120%;
        color: $black_color;

        @include below(620px) {
            font-size: 14px;
        }
    }

    &__btn {
        @include below(1024px) {
            flex: 0 0 40%;
            font-size: 16px;
            padding: 18px 10px;
        }

        @include below(620px) {
            flex: 0 0 100%;
            margin-top: 20px;
        }
    }

    &__burger-wrap {
        display: flex;
        justify-content: flex-end;
        flex: 0 0 40%;

        @include above(1025px) {
            display: none;
        }
    }

    &__burger {
        width: 32px;
        height: 32px;        
       
        

        &:not(.is-active) {
            [data-type="close"] {
                display: none;
            }
        }

        &.is-active {
            [data-type="burger"] {
                display: none;
            }
        }

        svg {
            width: 32px;
            height: 32px;
        }
    }
}