@import "main-bx";

.footer {
    padding: 70px 0 40px;

    @include below(620px) {
        padding: 40px 0 30px;
    }
    
    &__logo {
        display: flex;
        align-items: center;
        justify-content: center;

        &::before,
        &::after {
            content: "";
            display: inline-block;
            flex: auto;
            height: 1px;
            background-color: rgba(0, 0, 0, 0.5);
        }

        svg {
            margin: 0 70px;
            width: 50px;
            height: 50px;

            @include below(620px) {
                width: 30px;
                height: 30px;

                margin: 0 25px;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 10px;

        font-size: 15px;
        line-height: 120%;

        @include below(620px) {
            font-size: 12px;
            line-height: 120%;
        }
    }

    &__copy {
        color: rgba(2, 2, 2, 0.5);
        span {
            display: block;
            &:not(:last-child) {
                margin-bottom: 6px;

                @include below(620px) {
                    margin-bottom: 2px;
                }
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        color: #020202;

        svg {
            width: 16px;
            height: 16px;
            margin-left: 7px;
            transition: all .3s linear;

            @include below(620px) {
                width: 14px;
                height: 14px;
            }
        }

        &.is-active {
            svg {
                transform: rotate(180deg);
            }
        }
    }

    &__desc {
        margin-top: 40px;
        font-size: 14px;
        line-height: 140%;
        text-align: justify;
        color: rgba(2, 2, 2, 0.6);
        display: none;

        @include below(620px) {
            margin-top: 20px;
        }

        p {
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
}