@import "main-bx";

.fancybox-bg {
    background: transparent !important;
}

.fancybox-inner {
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(100px);
}

.modal {
    display: none;
    &__container {
        width: 100%;
        max-width: 636px !important;
        border-radius: 40px !important;

        @include below(500px) {
            border-radius: 10px !important;
        }

        &.fancybox-content {
            padding: 0;
            border-radius: 10px;
            overflow: visible !important;
        }

        .fancybox-button {
            top: 30px !important;
            right: 30px !important;
            left: auto;
            width: 24px;
            height: 24px;
        
            color: #020202 !important;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0 !important;

            @include below(500px) {
                top: 15px !important;
                right: 15px !important;
            }
        }
    }

    &__box {
        width: 100%;
        max-width: 416px;
        margin: 0 auto;
        background-color: #fff;
        border-radius: 10px;
        padding: 60px 0 46px;

        @include below(500px) {
            padding: 30px 15px;
            background-color: transparent;
        }
    }

    &__title {
        margin-bottom: 40px;

        @include below(500px) {
            text-align: center;
        }
    }

    &__form {
        .form__label {
            &:not(:last-of-type) {
                margin-bottom: 20px;
            }
        }

        .btn {
            margin-top: 40px;
        }
    }
}