@import "main-bx";

.title {
    &-lg {
        font-weight: 500;
        font-size: 68px;
        line-height: 90%;

        @include below(1240px) {
            font-size: 56px;
        }

        @include below(1024px) {
            font-size: 48px;
        }

        @include below(768px) {
            font-size: 38px;
        }
    }

    &-md {
        font-weight: 500;
        font-size: 54px;
        line-height: 90%;

        @include below(1240px) {
            font-size: 44px;
        }

        @include below(1024px) {
           font-size: 36px;
        }

        @include below(479px) {
            font-size: 26px;
        }
    }
}

.text {
    &-lg {
        font-weight: 300;
        font-size: 90px;
        line-height: 90%;
    }

    &-md {
        font-size: 20px;
        line-height: 90%;
    }

    &-sm {
        font-size: 18px;
        line-height: 140%;
    }

    &-xsm {
        font-size: 16px;
        line-height: 110%;
    }
}