@import "main-bx";

.btn {
    font-weight: 600;
    font-size: 16px;
    line-height: 90%;
    padding: 18px 40px;
    border-radius: 20px;
    transition: $trns;

    &_blue {
        background-color: $blue_color;
        color: $white_color;

        &:hover {
            background-color: #4494FF;
        }

        &:active {
            background-color: #4494FF;
        }
    }

    &_white {
        background-color: $white_color;
        color: $blue_color;
    }
}