@import "main-bx";

.intro {
    padding-top: 100px;

    @include below(1024px) {
        padding-top: 60px;
    }

    &__title {
        margin-bottom: 40px;
    }

    &__video-wrap {
        height: 22.8vw;

        @include below(1600px) {
            height: 30.8vw;
        }

        @include below(768px) {
            height: 245px;
        }

        @include below(500px) {
            height: 200px;
        }
    }

    .intro__video {
        position: relative;

        width: 100%;
        height: 100%;
        border-radius: 40px;
        overflow: hidden;

        object-fit: cover;
        

        @include below(500px) {
            border-radius: 20px;
        }

        .vjs-poster {
            background-position: center;
            background-size: cover;
        }

        .vjs-control-bar {
            background: transparent;
            height: auto;

            padding: 0 0 20px 20px;

            @include below(500px) {
                padding: 0 0 10px 10px;
            }
        }

        .vjs-play-control {
            width: 72px;
            height: 72px;
            background: rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(50px);
            border-radius: 30px;

            display: flex;
            align-items: center;
            justify-content: center;


            .vjs-icon-placeholder {
                display: block;
            }

            .vjs-icon-placeholder:before {
                font-size: 45px;

                @include below(500px) {
                    font-size: 25px;
                }
            }

            @include below(500px) {
                width: 40px;
                height: 40px;
                border-radius: 15px;

            }
        }

        .vjs-big-play-button {
            top: auto;
            bottom: 20px;
            left: 20px;
            width: 72px;
            height: 72px;
            background: rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(50px);
            border-radius: 30px;

            .vjs-icon-placeholder:before {
                margin-top: 12px;
                font-size: 45px;

                @include below(500px) {
                    margin-top: -4px;
                    font-size: 25px;
                }
            }


            @include below(500px) {
                width: 40px;
                height: 40px;
                border-radius: 15px;

                bottom: 10px;
                left: 10px;
            }
        }

        .vjs-volume-panel {
            order: 3;
            width: 72px;
            height: 72px;
            align-items: center;
            margin-left: auto;

            .vjs-icon-placeholder {
                &:before {
                    font-size: 25px;
                    line-height: 1;
                    margin-top: 25px;

                    @include below(500px) {
                        font-size: 20px;
                        margin-top: 10px;
                    }
                }
            }

            .vjs-volume-level {
                &:before {
                    top: -.35em
                }
            }

            @include below(500px) {
                width: 40px;
                height: 40px;
            }
        }

        .vjs-progress-control {
            flex: 0 0 80%;
            height: 72px;
            order: 2;
            margin: 0 auto;

            @include below(500px) {
                height: 40px;

                flex: 0 0 60%;
            }
        }

        .vjs-remaining-time,
        .vjs-picture-in-picture-control,
        .vjs-fullscreen-control {
            display: none !important;
        }

    }
}