@import "main-bx";

.form {

    &__label {
        display: block; 
        color: #020202;

        &:hover {
            .form__input {
                border-color: #020202;
            }
        }

        span {
            font-size: 12px;
            line-height: 15px; 
            display: block;
            margin-bottom: 6px;
        }

        &.is-error {
            .form__input {
                border-color: #F05353 !important;
            }
        }
    }

    &__label-error {
        font-size: 12px;
        line-height: 15px;
        color: #fff;
        background: #F05353;
        border-radius: 15px;
        padding: 8px 20px;
        margin-top: 4px;

        -webkit-animation-name: fadeIn;
        animation-name: fadeIn;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
    }

    &__input {
        width: 100%;
        padding: 15px 20px;
        border: 1px solid rgba(2, 2, 2, 0.2);
        transition: $trns;
        background-color: $white_color;
        border-radius: 20px;

        &:focus {
            border-color: #020202;
        }
    }

    &__btn {
      
    }

    &__checkbox {
        cursor: pointer;
        display: flex;
        align-items: center;
        color: $white_color;
        input {
            display: none;
        }
        input:checked {
            & ~ .form__checkbox-box svg {
                opacity: 1;
            }
        }
    }

    &__checkbox-box {
        width: 24px;
        min-width: 24px;
        height: 24px;
        background-color: $white_color;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        border: 1px solid currentColor;

        svg {
            opacity: 0;
            width: 12px;
            height: 12px;
        }
    }

    &__checkbox-text {
        color: currentColor;
        font-size: 12px;
        line-height: 23px;

        @include below(768px) {
            line-height: 1.5;
        }

        a {
            color: currentColor;
            text-decoration: underline;
        }
    }
}


@-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}
@keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
} 