@import "main-bx";

.capabilities {
    padding: 90px 0 120px;

    @include below(1240px) {
        padding: 40px 0 50px;
    }

    @include below(768px) {
        padding: 30px 0;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        @include below(1240px) {
            flex-direction: column;
            align-items: flex-start;
        }

        @include below(479px) {
            margin-bottom: 0;
        }
    }

    &__right {
        @include below(1240px) {
            margin-top: 40px;
        }

        @include below(479px) {
            margin-top: 32px;
        }

    }

    &__title {
        margin-bottom: 37px;

        @include below(1024px) {
            margin-bottom: 24px;
        }
    }

    &__text {
        font-size: 18px;
        line-height: 120%;
    }

    &__item {
        padding: 50px;
        border: 1px solid $blue_color;
        border-radius: 40px;
        display: flex;
        align-items: center;

        @include below(1024px) {
            padding: 30px;
        }

        @include below(479px) {
            padding: 20px 30px;
            border-radius: 20px;
        }

        @include below(365px) {
            padding: 15px;
        }
    }

    &__icon {
        width: 80px;
        height: 80px;
        background-color: $blue_color;
        box-shadow: 0px 4px 30px rgba(106, 168, 251, 0.5);
        border-radius: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 24px;

        @include below(1240px) {
            width: 40px;
            height: 40px;
            border-radius: 15px;
        }

        @include below(365px) {
            margin-right: 15px;
        }

        img {
            width: 40px;
            height: 40px;

            @include below(1240px) {
                width: 20px;
                height: 20px;
            }
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
    }

    &__text-lg {
        font-weight: 300;
        @include below(1240px) {
            font-size: 50px;
        }

        @include below(479px) {
            font-size: 30px;
        }
    }

    &__text-sm {
        font-size: 20px;
        line-height: 90%;
        color: rgba(2, 2, 2, 0.5);
        text-align: right;

        @include below(479px) {
            font-size: 12px;
            text-align: left;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin: -20px -10px 0;
     

        @include below(1024px) {
            margin: -20px 0 0;
        }

        @include below(479px) {
            margin: 0;
        }

        .capabilities__item {
            margin: 20px 10px 0;

            @include below(1024px) {
                margin: 20px 0 0;
                align-self: flex-start;
            }

            @include below(479px) {
                margin: 6px 0 0;
            }

            &:last-child {
                margin-left: 180px;

                @include below(1024px) {
                    margin: 20px 0 0;
                }

                @include below(479px) {
                    margin: 6px 0 0;
                }
            }
        }
    }
}